import React, {useState} from 'react';
import "./App.css";
import "./Home.css";
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import Axios from "axios";
import {Login, Sign} from "./login";
import {NavLink} from "react-router-dom";
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import ScreenSize from "./app_effect/ScreenSize";





function Home(props) {
    const [photo, setPhoto]=useState('');
    const photourl = `${process.env.REACT_APP_BOARDURL}`

    const onUploadEvent = (e) => {
        const uploadFile=new FormData();
        uploadFile.append("upload",e.target.files[0]);
        Axios({
            method:'post',
            url:'/upload',
            data:uploadFile,
            headers:{'Content-Type':'multipart/form-data'}
        }).then(res=>{
            setPhoto(res.data);
        })
    }



    return (
        <div className="homeBg">
            <ScreenSize/>
            <h2> .</h2>
            <input type='file' onChange={onUploadEvent}/>


            <img style={{width:'300px'}} alt={'testimg'} src={`${photourl}${photo}`} />
            <img style={{width:'300px'}} alt={'test2img'} src={`http://kr.object.ncloudstorage.com/bit701-bucket-111/birdiebuddy/${photo}`} />


            <ul> Chat
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>

            <ul> friend
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>

            <ul> hugi
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>

            <ul> joining
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>

            <ul>
                Login
                <li>
                    <NavLink to={"/login/login"}></NavLink>
                </li>
                <li>
                    <NavLink to={"/login/sign"}></NavLink>
                </li>
            </ul>

            <ul> mypage
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>

            <ul> score
                <li>
                    <NavLink to={''}></NavLink>
                </li>
            </ul>


            <br/>
            <br/>
            <button type='button' className={'btn_long'}></button>
            <br/>
            <br/>
            <button type='button' className={'btn_mid'}></button>
            <br/>
            <br/>
            <button type='button' className={'btn_short'}></button>
            <br/>
            <br/>
            <br/>
            <br/><br/>
            <br/><br/>
            <br/>


            <hr/>


            <div className={'main_headwrap'}>
                <div className={'main_logo'}>BirdieBuddy</div>
                <div className={'main_menu'}> <NotificationsNoneOutlinedIcon/> <img alt={'imsi'} src={''}/></div>
            </div>
            <div className={'main_banner'}>
                <div><img alt={''} src={''}/></div>
            </div>
            <div className={'main_btnwrap'}>
                <div>

                </div>
                <div>
                    <div></div>
                    <div></div>
                </div>
            </div>
            <div className={'main_joinreco'}>


            </div>

        </div>
    );
}

export default Home;