import React, {useEffect} from 'react';

function ScreenSize(props) {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`);

    useEffect(() => {
        ScreenSize();
    });
    return (
        <div>




        </div>
    );
}

export default ScreenSize;